@import "../sass/k-sass-core-only";

///
/// Default BN-SAS-CORE configuration settings.
/// @:Option: with "_" will be compile to !important
/// @:null: with null to leave the value
///

.rotate {
    /*+rotate(45deg)*/
    @include rotate(45deg); }

.rotate-1 {
    /*+rotate(45deg, 23,23,23,23)*/
    @include rotate(45deg, 23,23,23,23); }

.background-gradient {
    /*+background-gradient(#FFF, #DFDFDF)*/
    @include background-gradient(#FFF, #DFDFDF); }

.background-horizontal {
    /*+background-horizontal(color(main), color(link))*/
    @include background-horizontal(color(main), color(link)); }

.background-radial {
    /*+background-radial(color(main), 0%, color(link), 100%)*/
    @include background-radial(color(main), 0%, color(link), 100%); }

.animation {
    /*+animation(loading .5s)*/
    @include animation(loading .5s); }

.animation-5 {
    /*+delay(.5s)*/
    @include delay(.5s); }

.transform {
    /*+transform(scale(2,2))*/
    @include transform(scale(2,2)); }

.transform-origin {
    /*+transform(rotate(45deg))*/
    /*+transform-origin(20% 40%)*/
    @include transform(rotate(45deg));
    @include transform-origin(20% 40%); }

.transform-style {
    /*+transform-style(preserve-3d)*/
    @include transform-style(preserve-3d); }

.transition {
    /*+transition(all .5s)*/
    @include transition(all .5s); }

.filter {
    /*+filter(grayscale, 100%)*/
    @include filter(grayscale, 100%); }

.blur {
    /*+blur(5)*/
    @include blur(5); }

@include keyframes(pluse) {
    0% {
        @include opacity(0); }
    100% {
        @include opacity(1); } }
